body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body, html {height:100%;}
body {overflow: hidden; }
.World {
	height: 100%;
}


.App {
  min-height: 100vh;
  background-color: #26bdd0;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #1d93a2;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Toolbar {
  position: fixed;
  display: flex;
  align-items: center;
  top: 10px;
  opacity: 0.9;
/*  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);*/
  height: 60px;
  width: 100%;
  z-index: 2;
  color: white;
  font-weight: 600;
  font-size: 26px;
}

.Store {
  position: fixed;
  background-color: #D8D8D8;
  opacity: 0.9;
  /*box-shadow: 0 4px 20px 0 rgba(0, 0, 0, 0.1), 0 6px 40px 0 rgba(0, 0, 0, 0.09);*/
  height: 100%;
  width: 17%;
  z-index: 1;
  color: white;
  font-weight: 600;
  font-size: 26px;
  margin-top: 60px;
  padding-left: 20px;
}
.logo {
  display:inline-block;
  margin-left: 40px;
  height: 41px;
}
.logoimg {
  height: 40px;
}
.appName{
  display:inline-block;
  padding: 15px;
}

.storeSelect {
  margin-left: auto;
  width: 80%;
}

